import React, { useState } from "react";
import { useCart } from "../context/CartContext";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
// import { styled } from "@mui/material/styles";
import { Counter } from "../components/Counter";
import DeleteIcon from "@mui/icons-material/Delete";
import { IconButton } from "@mui/material";
// import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import DeliveryDiningIcon from "@mui/icons-material/DeliveryDining";
import {  useNavigate } from "react-router-dom";
import {  TableRow, Table, Avatar, TableBody, TableCell, TableHead, TableContainer } from '@mui/material';



export const Cart = () => {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const { setShowCart, setCart, cart, removeFromCart } = useCart();
  const total = calculateTotalPrice(cart);
  

  const navigate = useNavigate();

  function calculateTotalPrice(cart) {
    let total = 0;
    for (const item of cart) {
      total += item.product.precio * item.quantity;
    }
    return total;
  }

  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
    console.log(cart);
  };

  const handleClose = () => {
    setOpen(false);
    
    setShowCart(false);
    setCart([]);
    navigate("/");
  };

  return (
    <div style={{ height: "10px" }}>
      {cart.length > 0 ? (
        <div style={{ minWidth: "1000px" }}>
          <div
            style={{ display: "block", padding: "50px", textAlign: "center" }}
          >
            <div
              style={{
                backgroundColor: "white",
                position: "sticky",
                top: 0,
                zIndex: 99,
                border: "1px solid black",
                borderRadius: "10px",
                marginBottom: "20px",
                boxShadow: "1px 1px 5px 1px grey",
                color: "black",
                fontFamily: "monospace",
              }}
            >
              <h1>Tu Pedido</h1>
            </div>
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                  <TableHead sx={{position:'sticky', top:0}}>
                    <TableRow>
                      <TableCell></TableCell>
                      <TableCell>Nombre</TableCell>
                      <TableCell >Modo venta</TableCell>
                      <TableCell>Precio x unidad</TableCell>
                      <TableCell>Cantidad</TableCell>
                      <TableCell >Precio</TableCell>
                      <TableCell ></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {cart.map((cartItem) => (
                    
                      <TableRow
                        key={cartItem._id}
                        sx={{
                          "&:last-child td, &:last-child th": { border: 0 },
                        }}
                      >
                        <TableCell>
                          {" "}
                          <Avatar alt="Product Image" src={cartItem.product.url} />
                        </TableCell>
                        <TableCell
                          sx={{ fontWeight: "800" }}
                          component="th"
                          scope="cartItem"
                        >
                          {cartItem.product.name}
                        </TableCell>
                        <TableCell align="left">{cartItem.product.modalidadVenta}</TableCell>
                        <TableCell align="left">$ {cartItem.product.precio}</TableCell>
                        <TableCell ><Counter cartItem={cartItem} /></TableCell>
                        <TableCell align="left">$ {cartItem.quantity * cartItem.product.precio}</TableCell>
                        <TableCell align="left">
                        <IconButton
                           color="error"
                           aria-label="Delete"
                           size='large'
                           onClick={() => removeFromCart(cartItem)}
                       >
                           <DeleteIcon fontSize='large' />
                       </IconButton>
                       </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            
            <div
              style={{
                backgroundColor: "white",
                position: "sticky",
                bottom: 53,
                zIndex: 99,
                display: "flex",
                justifyContent: "center",
                border: "1px solid black",
                borderRadius: "10px",
                }}
            >
              <div style={{ display: "flex", textAlign: "center" }}>
                <p
                  style={{
                    marginLeft: "10px",
                    fontFamily: "sans-serif",
                    marginRight: "50px",
                    fontSize: "20px",
                    fontWeight: "800",
                  }}
                >
                  Total de la compra{" "}
                </p>
                <p
                  style={{
                    fontFamily: "sans-serif",
                    fontSize: "20px",
                    fontWeight: "800",
                    color: "green",
                  }}
                >
                  $ {total.toFixed(2)}
                </p>
                <div style={{ padding: "10px" }}>
                  <Button
                    variant="contained"
                    sx={{ height: "100%", marginLeft: "20px" }}
                    onClick={handleOpen}
                  >
                    FinalizarCompra
                  </Button>
                  <Modal
                    open={open}
                    onClose={handleClose}
                    aria-labelledby="modal-modal-title"
                    aria-describedby="modal-modal-description"
                  >
                    <Box sx={style}>
                      <Typography
                        id="modal-modal-title"
                        variant="h5"
                        component="h2"
                      >
                        Tu pedido ha sido creado con xito{" "}
                        <CheckCircleIcon
                          fontSize="small"
                          color="success"
                        ></CheckCircleIcon>
                      </Typography>
                      <Typography id="modal-modal-description" sx={{ mt: 6 }}>
                        Tu pedido llegar luego de las 20 Hs{" "}
                        <DeliveryDiningIcon fontSize="medium"></DeliveryDiningIcon>
                      </Typography>
                    </Box>
                  </Modal>
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

